.messages {
    display: block;
    position: relative;
    padding: 0 12px;
    height: 22px;
    line-height: 26px;
    font-size: 11px;
    font-weight: bold;
    color: #666;
    text-decoration: none;
    text-shadow: 0 1px white;
    background: #FAFAFA;
    width: 100px;
    box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.1);
    margin: 15px auto;
  }

  .messages .unread_count {
    display: block;
    position: absolute;
    top: -10px;
    right: 3px;
    line-height: 14px;
    height: 14px;
    padding: 0 5px;
    font-family: Arial, sans-serif;
    font-size: 9px;
    color: white;
    text-shadow: 0 1px rgba(0, 0, 0, 0.25);
    border: 1px solid;
    border-radius: 10px;
    box-shadow: inset 0 1px rgba(255, 255, 255, 0.3), 0 1px 1px rgba(0, 0, 0, 0.08);
    background: #67c1ef;
    border-color: #30aae9;
  }